<template>
  <div>
    <el-dialog
      @opened="openDialog"
      :width="dialogWidth"
      :title="title"
      :visible.sync="showEditDialog"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <el-form
        :model="forms"
        :rules="rules"
        ref="editForms"
        class="edit-formsdemo-form-inline"
        label-position="left"
        :label-width="labelWidth"
      >
        <el-form-item label="订单编号" prop="deliveryOrderSn">
          <el-input v-model="forms.deliveryOrderSn" placeholder="订单编号" readonly></el-input>
        </el-form-item>
        <el-form-item label="用户名" prop="username">
          <el-input v-model="forms.username" placeholder="用户名" readonly></el-input>
        </el-form-item>
        <el-form-item label="收货人" prop="deliveryName">
          <el-input v-model="forms.deliveryName" placeholder="收货人" readonly></el-input>
        </el-form-item>
        <el-form-item label="收货电话" prop="deliveryMobile">
          <el-input v-model="forms.deliveryMobile" placeholder="收货电话" readonly></el-input>
        </el-form-item>
        <el-form-item label="收货地址" prop="deliveryAddress">
          <el-input v-model="forms.deliveryAddress" placeholder="收货地址" readonly></el-input>
        </el-form-item>
        <el-form-item label="物流公司" prop="deliveryLogiCode">
          <div style="display:flex">
            <el-select
              v-model="forms.deliveryLogiCode"
              style="width: 250px"
              placeholder="物流公司"
              size="small"
            >
              <el-option
                v-for="(item,index) in expressComapnyList"
                :key="index"
                :label="item.expressCorpName"
                :value="item.expressCorpId"
              ></el-option>
            </el-select>
            <el-input
              v-model="forms.deliveryLogiFee"
              placeholder="物流费"
              size="small"
              style="width:100px;margin-left:50px"
            ></el-input>
          </div>
        </el-form-item>
        <el-form-item label="物流单号" prop="deliveryLogiNo">
          <el-input v-model="forms.deliveryLogiNo" placeholder="物流单号"></el-input>
        </el-form-item>
        <el-form-item label="快递物流信息" prop="deliveryLogiInformation">
          <el-input v-model="forms.deliveryLogiInformation" placeholder="快递物流信息"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="forms.remark" placeholder="备注"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$emit('close')">取 消</el-button>
        <el-button type="primary" @click="submit">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Required } from "@/utils/validateRules";
import {
  editDelivery,
  loadExpressComapny
} from "@/api/logistics/logisticsManage";
import { fillerLeft, resetObject } from "@/utils/common";
export default {
  props: {
    title: {
      type: String,
      default: "编辑"
    },
    dialogWidth: {
      type: String,
      default: "700px"
    },
    labelWidth: {
      type: String,
      default: "120px"
    },
    showEditDialog: Boolean
  },
  data() {
    return {
      forms: {
        deliveryOrderSn: "",
        deliveryUserId: "",
        deliveryLogiCode: "",
        deliveryLogiNo: "",
        deliveryLogiInformation: "",
        ldeliveryLogiStatus: "",
        deliveryAddress: "",
        deliveryName: "",
        deliveryMobile: "",
        deliveryConfirmTime: "",
        deliveryLogiFee: "",
        deliveryStatus: "",
        remark: "",
        username: ""
      },
      expressComapnyList: [],
      rules: {
        deliveryLogiNo: Required,
        username: Required
      }
    };
  },

  methods: {
    submit() {
      this.$refs.editForms.validate(valid => {
        if (valid) {
          editDelivery(this.forms).then(data => {
            if (data.code == 0) {
              this.$message.success("更新成功！");
              this.$emit("success");
              return;
            }
            this.$message.error(data.msg);
          });
          return;
        }
        this.$message.error("请按照提示正确填写内容！");
        return false;
      });
    },
    openDialog() {},
    update(id, data) {
      //可以外部填充回写做编辑用，也可以请求详情接口填充表单
      this.forms = {
        deliveryOrderSn: data.orderSn,
        deliveryUserId: data.orderMemberId,
        deliveryLogiCode: "",
        deliveryLogiNo: "",
        deliveryLogiInformation: "",
        deliveryAddress: data.orderReceiveAddr,
        deliveryName: data.orderReceiveName,
        deliveryMobile: data.orderReceivePhone,
        deliveryConfirmTime: "",
        deliveryLogiFee: 0,
        deliveryStatus: "",
        remark: "",
        username: data.username
      };
    },
    async loadExpressComapny() {
      let data = await loadExpressComapny();
      if (data.code == 0) {
        this.expressComapnyList = data.data;
        return;
      }
      this.$message.error(data.msg);
    }
  },
  mounted() {
    this.loadExpressComapny();
  }
};
</script>
