<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="订单编号"
          size="small"
          style="width: 280px"
          v-model="searchParams.orderSn"
          clearable
        ></el-input>
        <el-select
          v-model="searchParams.orderType"
          clearable
          style="width: 140px"
          placeholder="订单类型"
          size="small"
        >
          <el-option v-for="(k,v,index) in orderTypeEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-input
          placeholder="用户名"
          size="small"
          style="width: 140px"
          v-model="searchParams.username"
          clearable
        ></el-input>
        <el-select
          v-model="searchParams.orderStatus"
          clearable
          style="width: 140px"
          placeholder="状态"
          size="small"
        >
          <el-option v-for="(k,v,index) in orderStatusEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-select
          v-model="searchParams.orderConfirmStatus"
          clearable
          style="width: 140px"
          placeholder="是否收货"
          size="small"
        >
          <el-option
            v-for="(k,v,index) in orderConfirmStatusEnum"
            :key="index"
            :label="k"
            :value="v"
          ></el-option>
        </el-select>
        <el-select
          v-model="searchParams.orderPayType"
          clearable
          style="width: 140px"
          placeholder="支付类型"
          size="small"
        >
          <el-option v-for="(k,v,index) in orderPayTypeEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-date-picker
          v-model="searchParams.dateRange"
          size="small"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
        ></el-date-picker>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form>
            <el-form-item label="收货人">
              <span v-html=" props.row.orderReceiveName"></span>
            </el-form-item>
            <el-form-item label="收货人电话">
              <span v-html=" props.row.orderReceivePhone"></span>
            </el-form-item>
            <el-form-item label="收货人地址">
              <span v-html=" props.row.orderReceiveAddr"></span>
            </el-form-item>
            <el-form-item label="发货时间">
              <span v-html=" props.row.orderDeliveryTime"></span>
            </el-form-item>
            <el-form-item label="确认收货时间">
              <span v-html=" props.row.orderConfirmTime"></span>
            </el-form-item>
            <el-form-item label="支付时间">
              <span v-html=" props.row.orderPayTime"></span>
            </el-form-item>
            <el-form-item label="更新时间">
              <span v-html=" props.row.updateTime"></span>
            </el-form-item>
            <el-form-item label="更新人">
              <span>{{ props.row.updateBy }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="orderSn" label="订单编号" width="280"></el-table-column>
      <el-table-column align="center" prop="username" label="用户名" width="120"></el-table-column>
      <el-table-column align="center" prop="orderTotalAmount" label="总金额" width="80"></el-table-column>
      <el-table-column align="center" prop="orderFreightAmount" label="运费" width="80"></el-table-column>
      <el-table-column align="center" prop="orderPayAmount" label="实付" width="80"></el-table-column>
      <el-table-column
        align="center"
        prop="orderType"
        label="订单类型"
        width="100"
        :formatter="orderTypeFormatter"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="orderPayType"
        label="支付类型"
        width="100"
        :formatter="orderPayTypeFormatter"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="orderConfirmStatus"
        label="收货"
        width="80"
        :formatter="orderConfirmStatusFormatter"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="orderStatus"
        label="状态"
        width="100"
        :formatter="orderStatusFormmater"
      ></el-table-column>

      <el-table-column align="center" prop="createTime" label="创建时间" width="155"></el-table-column>
      <el-table-column align="center" label="操作">
        <div slot-scope="s">
          <el-button type="primary" size="small" @click="edit(s.row)">订单详情</el-button>
          <el-button
            v-if="s.row.orderStatus==1"
            type="danger"
            size="small"
            @click="delivery(s.row)"
          >物流发送</el-button>
          <el-button
            type="danger"
            size="small"
            @click="changeOrderStatus(s.row)"
            v-if="s.row.orderStatus!=5"
          >置订单无效</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getOrderList"
      ref="pagination"
      @returnData="returnData"
    />
    <OrderDeliveryEdit
      :title="titie"
      ref="edit"
      :showEditDialog="showEditDialog"
      @close="showEditDialog = false"
      @success="refresh"
    />
  </div>
</template>

<script>
import { getOrderList, editOrderStatus } from "@/api/order/orderManage";
import { getDateList } from "@/utils/date";
import { resetObject } from "@/utils/common";
import OrderDeliveryEdit from "./OrderDeliveryEdit";
import {
  OrderConfirmStatusEnum,
  OrderStatusEnum,
  OrderPayTypeEnum,
  OrderTypeEnum
} from "@/utils/Immutable";
export default {
  components: { OrderDeliveryEdit },
  data() {
    return {
      searchParams: {
        username: "",
        orderSn: "",
        orderStatus: "",
        orderConfirmStatus: "",
        orderPayType: "",
        dateRange: []
      },
      orderConfirmStatusEnum: OrderConfirmStatusEnum,
      orderStatusEnum: OrderStatusEnum,
      orderPayTypeEnum: OrderPayTypeEnum,
      orderTypeEnum: OrderTypeEnum,
      getOrderList: getOrderList,
      titie: "",
      total: 10,
      pickerOptions: getDateList(),
      showEditDialog: false,
      tableData: []
    };
  },
  methods: {
    orderConfirmStatusFormatter(a, b, v) {
      return this.orderConfirmStatusEnum[v];
    },
    orderStatusFormmater(a, b, v) {
      return this.orderStatusEnum[v];
    },
    orderTypeFormatter(a, b, v) {
      return this.orderTypeEnum[v];
    },
    orderPayTypeFormatter(a, b, v) {
      return this.orderPayTypeEnum[v];
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    changeOrderStatus(v) {
      v.orderStatus = 5;
      let text = `您确定要修改订单状态为${this.orderStatusEnum[v.orderStatus]}`;
      let _this = this;
      this.$confirm(text, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(s => {
          _this.editOrder(v);
        })
        .catch(() => {
          _this.refresh();
        });
    },
    async editOrder(v) {
      let data = await editOrderStatus(v);
      if (data.code != 0) {
        this.$message.error(v.msg);
        this.refresh();
      } else {
        this.$message.success("修改成功");
      }
      return data;
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    },
    returnData(data) {
      this.tableData = data;
    },
    edit(row) {
      this.$router.push({ name: "detail", query: { orderSn: row.orderSn } });
    },
    delivery(row) {
      this.titie = "物流发送";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, row);
    }
  }
};
</script>
