import request from "@/utils/request.js";
/**
 * 物流公司
 * @param params
 * @returns {AxiosPromise}
 */
export function getExpressComapnyList(params) {
    return request({
        url: "/apiadmin/logistics/findcompanylist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function editExpressComapny(params) {
    return request({
        url: "/apiadmin/logistics/editexpresscomapny",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}

export function loadExpressComapny() {
    return request({
        url: "/apiadmin/logistics/findexpresslist",
        method: "get",
    });
}
export function editDelivery(params) {
    return request({
        url: "/apiadmin/logistics/editdelivery",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}
